import { Service } from "@/services/service";
import { find, isEmpty } from "lodash";
import { ref } from "vue";
import UserProp from "@/composables/userproperty"

export enum KitProp {
    TEST_KIT_USER_PROPERTY = 'hts_user_assigned_kits',
    HIV_SELF_KIT = 'HIV self-kit',
    SYPHILIS = 'Syphilis',
    HEPATITIS = 'Hepatitis B',
    BIOLINE = 'Bioline',
    DETERMINE = 'Determine',
    UNIGOLD = 'Unigold'
}

export function TestKit() {
    const kits = ref<any>([])
    const { getUserProp, setUserProp } = UserProp()

    const getSelectedTests = async () => {
        try {
            const req = await getUserProp('HTS_PROGRAMS')
            return `${req||''}`.split(',').map((prop: string) => prop.trim())
        } catch (e) {
            console.error(`${e}`)
        }
        return []
    }

    const initKits = async () => {
        const defaultKits = [
            {
                kitName: KitProp.UNIGOLD,
                lotNo: '',
                expiryDate: '',
                testGroup: 'profession_hiv_kit',
                visible: true,
                userProp: "HIV Test"
            },
            {
                kitName: KitProp.DETERMINE,
                lotNo: '',
                expiryDate: '',
                testGroup: 'profession_hiv_kit',
                visible: true,
                userProp: "HIV Test"
            },
            {
                kitName: KitProp.BIOLINE,
                lotNo: '',
                expiryDate: '',
                testGroup: 'profession_hiv_kit',
                visible: true,
                userProp: "HIV Test"
            },
            {
                kitName: KitProp.HIV_SELF_KIT,
                lotNo: '',
                expiryDate: '',
                visible: true
            },
            {
                kitName: KitProp.SYPHILIS,
                lotNo: '',
                expiryDate: '',
                visible: true,
                userProp: "Syphilis Test"
            },
            {
                kitName: KitProp.HEPATITIS,
                lotNo: '',
                expiryDate: '',
                visible: true,
                userProp: "Hepatitis B Test"
            }
        ]
        try {
            const selectedKits = await getSelectedTests()
            const req: any = await getUserProp(KitProp.TEST_KIT_USER_PROPERTY)
            const prevKits = JSON.parse(req||'')

            defaultKits.forEach((kit: any) => {
                if (kit.userProp && !isEmpty(selectedKits)) {
                    kit.visible = selectedKits.includes(kit.userProp)
                }
                const preset = find(prevKits, { kitName: kit.kitName })
                if (preset) {
                    kit.lotNo = preset.lotNo
                    kit.expiryDate = preset.expiryDate
                }
            })
            kits.value = defaultKits.filter((k: any) => k.visible)
        } catch (e) {
            kits.value = defaultKits
            console.error(`${e}`)
        }
        
    }

    const getLotNo = (kitName: KitProp) => {
        const data = find(kits.value, { kitName })
        if (data) {
            if(new Date(data.expiryDate) <= new Date(Service.getSessionDate())) {
                return ''
            }
            return data.lotNo
        } 
        return ''
    }

    const getExpiryDate = (kitName: KitProp) => {
        const date = find(kits.value, { kitName })
        if (date) {
            if (new Date(date.expiryDate) <= new Date(Service.getSessionDate())) {
                return ''
            }
            return date.expiryDate
        }
        return ''
    }

    const saveKits = () => setUserProp(KitProp.TEST_KIT_USER_PROPERTY, JSON.stringify(kits.value))

    return {
        kits,
        initKits,
        saveKits,
        getLotNo,
        getExpiryDate
    }
}
