
import EncounterMixinVue from "@/views/EncounterMixin.vue";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { defineComponent } from "vue";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { IonPage } from "@ionic/vue";
import { HtsTestingService } from "@/apps/HTS/services/hts_testing_service";
import { infoActionSheet } from "@/utils/ActionSheets";
import { PatientProgramService } from "@/services/patient_program_service";
import Store from "@/composables/ApiStore";
import keyboard from "@/utils/PopupKeyboard";
import { find } from "lodash";
import { toDate } from "@/utils/Strs";
import { KitProp, TestKit } from "../../composables/testKits";
import UserProp from "@/composables/userproperty"
import { alertConfirmation } from "@/utils/Alerts";

export default defineComponent({
  components: { IonPage },
  mixins: [EncounterMixinVue],
  data: () => ({
    service: {} as HtsTestingService,
    clientAge: -1
  }),
  watch: {
    ready: {
      handler(ready: boolean) {
        if (ready) {
          this.service = new HtsTestingService(this.patientID, this.providerID);
          this.fields = [
            this.accessPointTypeField(),
            this.facilityAccessPointField(),
            this.communityAccessPoints(),
            this.testsOfferedField(),
            this.lastHivTestResultField(),
            this.lastHivTestTypeField(),
            this.lastTimeHIVTestedField(),
            this.arvHistoryField(),
            this.drugTakenField(),
            this.mostRecentDrugHiddenField(),
            this.timeSinceDrugTaken(),
            this.clientRiskCategoryField(),
            this.testResultsField(),
            this.hivTestKitDetailsField(),
            this.hivResultHiddenField(),
            this.hivPatientCategoryField()
          ];
        }
      },
      immediate: true
    },
  },
  methods: {
    onFinish(formData: any, computedData: any) {
      this.saveFormEncounterAndProceed({
        encounterTypeID: this.service.encounterTypeID,
        formData,
        computedData,
        callback: async () => {
          try{
            await new PatientProgramService(this.patientID).enrollProgram();
            Store.invalidate("PATIENT_PROGRAM");
          } catch (e) {
            console.log("Program enrollment error: " + e)
          }
        }
      })
    },
    /**
     * HIV tests can be marked as incomplete if the client tests Positive
     * on the first test and does not complete the other three or two tests to confirm the positive results.
     * This may happen if the facility runsout of test kits or the client walksout without completing 
     * tests.
     * @param testingFieldData 
     */
    threeTierHIVtestNotComplete(testingFieldData: Option[]) {
      let testOnePositive = false
      let isIncompleteThreeTier = false
      for (const opt of testingFieldData) {
        if (opt.other.id === 'test_1' && opt.value === 'Positive') {
          testOnePositive = true
        } else if (
          /test_2|test_1_repeat|test_3/i.test(`${opt?.other?.id}`) && 
          opt.value === '' && !opt.disabled
        ) {
          isIncompleteThreeTier = true
        }
      } 
      return testOnePositive && isIncompleteThreeTier
    },
    validateTimeUnit(v: Option) {
      const maxDaysAllowed = 90;
      const maxWeeksAllowed = 12;
      const maxMonthsAllowed = 24;

      if (parseInt(`${v.other.value}`) <= 0 && !/day/i.test(v.other.timeUnit)) {
        return [`Enter ${v.other.timeUnit} greater than zero`];
      }
      if(v.other.timeUnit === 'Days' && parseInt(`${v.other.value}`) > maxDaysAllowed) {
        return [`Enter ${v.other.timeUnit} less than ${maxDaysAllowed}, use Weeks Instead`];
      }
      if(v.other.timeUnit === 'Weeks' && parseInt(`${v.other.value}`) > maxWeeksAllowed) {
        return [`Enter ${v.other.timeUnit} less than ${maxWeeksAllowed}, use Months Instead`];
      }
      if(v.other.timeUnit === 'Months' && parseInt(`${v.other.value}`) > maxMonthsAllowed) {
        return [`Enter ${v.other.timeUnit} less than ${maxMonthsAllowed}, use Years Instead`];
      }
      if (v.other.value.length > 1 && v.other.value[0] === "0") {
        return [`Value ${v.other.value} is invalid!`];
      }
      if (new Date(v.other.date) < new Date(this.facts.birthDate)) {
        return [
          `${
            v.label
          } is greater than client's age of ${this.facts.age} Years`,
        ];
      }
      return null;
    },
    inclonclusiveResultDialog() {
      return infoActionSheet(
        "Reschedule test",
        "",
        "HIV result is inconclusive, schedule another test in 2 weeks",
        [{ name: "Ok", slot: "start" }]
      );
    },
    testOffered(formData: any, test: string) {
      return formData.tests_offered.some((v: Option) => v.value === test);
    },
    result(formData: any, test: string) {
      return find(formData.test_results, { other: { id: test } }) || {}
    },
    accessPointTypeField(): Field {
      return {
        id: "accesspoint_type",
        helpText: "Access point type",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded("HTS Access Type", v.value),
          };
        },
        options: () => {
          return [
            {label:"Health Facility", value:"Health Facility"},
            { label: "Community", value: "Community" },
            { label: "Facility Referred from Community", value: "Health Facility" }
          ];
        },
      };
    },
    facilityAccessPointField(): Field {
      return {
        id: "facility_access_points",
        helpText: "Health facility access points",
        type: FieldType.TT_SELECT_GRID,
        isRequired: () => true,
        condition: (f: any) => f.accesspoint_type.value === "Health Facility",
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueText(
              "Location where test took place",
              `${v.value}`
            ),
          };
        },
        config: {
          columnsPerRow: 2
        },
        options: () => {
          return [
            {label: "1 | VCT", value: "VCT"},
            {label: "2 | ANC", value: "ANC First Visit"},
            {label: "3 | Inpatient", value: "Inpatient"},
            {label: "4 | STI", value: "STI"},
            {label: "5 | PMTCT-Follow-up (ANC FUP, Mat, Post-nat, BF)", value: "PMTCT FUP"},
            {label: "6 | Index", value: "Index"},
            {label: "7 | Paediatric", value: "Paediatric"},
            {label: "8 | VMMC", value: "VMMC"},
            {label: "9 | Malnutrition", value: "Malnutrition"},
            {label: "10 | TB", value: "TB"},
            {label: "11 | OPD", value: "OPD"},
            {label: "12 | Other PITC (PrEP, dental e.t.c)", value: "Other"},
            {label: "18 | SNS (Social Network Strategies)", value: "SNS"},
          ];
        },
      };
    },
    communityAccessPoints(): Field {
      return {
        id: "community_access_points",
        helpText: "Community access points",
        type: FieldType.TT_SELECT,
        condition: (f: any) => f.accesspoint_type.value === "Community",
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueText(
              "Location where test took place",
              `${v.value}`
            ),
          };
        },
        options: () => {
          return [
            {label: "13 | VCT", value: "VCT"},
            {label: "14 | Index", value: "Index"},
            {label: "15 | VMMC", value: "VMMC"},
            {label: "16 | Other", value: "Other"},
            {label: "17 | SNS", value: "SNS"},
            {label: "19 | Mobile", value: "Mobile"},
          ];
        },
      };
    },
    testsOfferedField(): Field {
      let availableTests: string[] = [
        'HIV',
        'Syphilis',
        'Hepatitis B'
      ]
      return {
        id: "tests_offered",
        helpText: "Tests to be offered today?",
        type: FieldType.TT_MULTIPLE_SELECT,
        init: async () => {
          try {
            const { getUserProp } = UserProp()
            const res = await getUserProp('HTS_PROGRAMS')
            const tests = res.split(',').filter(
              (prop: string) => /HIV|syphilis|hepatitis/i.test(prop)
            ).map((test: string) => test.replace(/test/i, '').trim())
            if (tests.length >= 1) availableTests = tests
          } catch (e) {
            console.error(e)
            return false
          }
          return true
        },
        isRequired: () => true,
        condition: () => availableTests.length > 1,
        defaultOutput: () => this.mapStrToOptions(availableTests),
        options: () => this.mapStrToOptions(availableTests),
        config: {
          buildOptionsOnce: true
        },
      };
    },
    lastHivTestResultField(): Field {
      return {
        id: "last_hiv_result",
        helpText: "Last HIV Test",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded(
              "Previous HIV Test Results",
              v.value
            ),
          };
        },
        options: () => {
          return this.toConditionalOptions([
            ["Never Tested"],
            ["Negative"],
            ["Positive"],
            ["Exposed infant"],
            ["Invalid or inconclusive"],
          ]);
        },
      };
    },
    lastHivTestTypeField(): Field {
      return {
        id: "last_hiv_perfomed",
        helpText: "Last HIV test performed",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        condition: (f: any) =>
          !["Never Tested", "Exposed infant"].includes(f.last_hiv_result.value),
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded(
              "Previous HIV test done",
              v.value
            ),
          };
        },
        defaultOutput: (f: any) => {
          if (/exposed infant/i.test(f.last_hiv_result.value)) {
            return this.toOption('Professional')
          }
        },
        options: (f: any) => {
          return this.toConditionalOptions([
            ["Self"],
            [
              "Initial professional",
              !/Negative|Inconclusive/i.test(f.last_hiv_result.value),
            ],
            ["Professional"],
          ]);
        },
      };
    },
    lastTimeHIVTestedField(): Field {
      return {
        id: "last_time_hiv_tested",
        helpText: "Time since last HIV test",
        type: FieldType.TT_AGE_INPUT,
        condition: (f: any) =>
          f.last_hiv_result.value && f.last_hiv_result.value != "Never Tested",
        isRequired: () => true,
        beforeNext: async (v: Option, f: any) => {
          if (v.other.monthsAgoInt >= 12 && !this.testOffered(f, "HIV")) {
            await infoActionSheet(
              "Recommendation Alert",
              "",
              "Please consider testing client for HIV",
              [
                {
                  name: "Ok",
                  slot: "start",
                  color: "primary",
                },
              ]
            );
          }
          return true;
        },
        validation: (v: Option) => this.validateTimeUnit(v),
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildObs("Time of HIV test", {
              'value_datetime': v.other.date,
              'value_text': v.value
            })
          }
        },
        config: {
          excludeUnits: ["Hours"],
        },
      };
    },
    arvHistoryField(): Field {
      return {
        id: "last_taken_drugs",
        helpText: "Ever taken the following drugs?",
        type: FieldType.TT_MULTIPLE_YES_NO,
        isRequired: () => true,
        finalComputedValue: (v: Option[]) => {
          return {
            obs: v.map((d) =>
              this.service.buildValueCoded(d.other.concept, d.value)
            ),
          };
        },
        options: (f: any) => {
          if (f.last_taken_drugs) return f.last_taken_drugs;
          return [
            this.toYesNoOption("PrEP or infant NVP", {
              concept: "Taken Prep before",
            }),
            this.toYesNoOption("ART", { concept: "Taken ARV Before" }),
            this.toYesNoOption("PEP", { concept: "Taken PEP before" }),
          ];
        },
      };
    },
    drugTakenField(): Field {
      /**
       * Select Most recent drug from list of drugs
       */
      return {
        id: "drug_taken",
        helpText: "Most recent drug taken",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        condition: (f: any) => {
          let drugCount = 0 
          for(const drug of f.last_taken_drugs) {
            if (drug.label === 'ART' && drug.value === 'Yes') {
              return false
            }
            if (drug.value === 'Yes') ++drugCount
          }
          return drugCount > 1
        },
        defaultOutput: (f: any) => {
          const artSelected = f.last_taken_drugs.find((d: Option) => d.label === 'ART')
          if (artSelected) return artSelected
        },
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded(
              "Most recent drug taken",
              v.value
            ),
          };
        },
        options: (f: any) => {
          return f.last_taken_drugs
            .filter((d: Option) => d.value === "Yes")
            .map((d: Option) => this.toOption(d.label));
        },
      };
    },
    mostRecentDrugHiddenField(): Field {
      /**
       * Automatically determines recent drug if only one drug is Yes
       */
      return {
        id: "most_recent_drug",
        helpText: "Most recent drug",
        type: FieldType.TT_HIDDEN,
        condition: (f: any) =>
          f.last_taken_drugs.filter((d: Option) => d.value === "Yes").length ===
          1,
        defaultValue: (fdata?) => {
          return find(fdata.last_taken_drugs, { value: "Yes" });
        },
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded(
              "Most recent drug taken",
              v.value
            ),
          };
        },
      };
    },
    timeSinceDrugTaken(): Field {
      return {
        id: "time_since_last_drug_taken",
        helpText: "Time since last taken medication",
        type: FieldType.TT_AGE_INPUT,
        condition: (f: any) =>
          f.last_taken_drugs.some((d: Option) => d.value === "Yes"),
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildObs("Time since last taken medication", {
              value_text: `${v.value} ago`,
              value_datetime: v.other.date,
            }),
          };
        },
        validation: (v: Option) => this.validateTimeUnit(v),
        isRequired: () => true,
        config: {
          excludeUnits: ["Hours"],
        },
      };
    },
    clientRiskCategoryField(): Field {
      return {
        id: "client_risk_category",
        helpText: "Client Risk Category",
        type: FieldType.TT_SELECT,
        isRequired: () => true,
        finalComputedValue: (v: Option) => {
          return {
            obs: this.service.buildValueCoded("client risk category", v.value),
          };
        },
        options: () => {
          return this.mapStrToOptions([
            "Low risk",
            "On-going risk",
            "High risk event in last 3 months",
            "Risk assessment not done",
          ]);
        },
      };
    },
    hivTestKitDetailsField(): Field {
      return {
        id: "test_kit",
        helpText: "Kit information",
        type: FieldType.TT_INPUT_ARRAY,
        condition: (f: any) => f.test_results.filter((t: Option) => t.value!='')
          .some((t: Option) => !(t.other.expiryDate && t.other.lotNo)),
        config: {
          columns: [
            { label: "Kit Name" },
            { label: "Kit Lot No." },
            { label: "Kit Expiry Date" },
          ],
        },
        summaryMapValue: (v: Option) => {
          return {
            label: `Kit: ${v.label}`,
            value: `Lot No: ${v.value || v.other.fields[1].value}`
          }
        },
        validation: (v: Option[]) => {
          const data = v.map((d: Option) => d.other.fields);
          const someIncomplete = data.some((d: Option[]) => {
            const count = d.filter((x: Option) => x.value != "").length;
            return count > 0 && count < 3;
          });
          if (someIncomplete) {
            return ["Some rows have incomplete data!"];
          }
          const incomplete = data.some((d: Option[]) => {
            return d.some((x: Option) => x.value === "");
          });
          if (incomplete) {
            return [`Some rows are missing data`];
          }
          return null;
        },
        finalComputedValue: (v: Option[]) => {
          return {
            obs: v
              .map((d) => d.other.fields)
              .map(async (options: Option[]) => {
                const [parentObs, ...childObs] = options
                  .filter((l: Option) => typeof l.other.obs === "function")
                  .map((d: Option) => d.other.obs(d));
                return {
                  ...(await parentObs),
                  child: await Promise.all(childObs),
                };
              }),
          };
        },
        defaultOutput: (f: any) => {
          const kits = f.test_results.filter((r: Option) => r.value!='')
          if (kits.every((t: Option) => t.other.expiryDate && t.other.lotNo)) {
            return kits.map((v: Option) => {
              return {
                label: v.label,
                value: v.other.lotNo,
                other: {
                  fields: [
                    {
                      label: 'Kit name',
                      value: v.label,
                      other: {
                        obs: () => this.service.buildValueText("Kit name", v.label as string),
                      }
                    },
                    {
                      label: 'Kit Lot No',
                      value: v.other.lotNo,
                      other: {
                        obs: () => {
                          return this.service.buildValueText("Kit lot number", v.other.lotNo as string)
                        } 
                      }
                    },
                    {
                      label: 'Kit expiry date',
                      value: v.other.expiryDate,
                      other: {
                        obs: () => this.service.buildValueDate("kit expiry date", v.other.expiryDate as string),
                      }
                    }
                  ]
                }
              }
            })
          }
        },
        options: async (f: any) => {
          const testResults: Option[] = Array.isArray(f.test_results) ? f.test_results : [];
          const kitNameOption = (kitName: string) => ({
            label: "Kit Name",
            value: kitName,
            clearable: false,
            other: {
              usefontBold: true,
              obs: (v: Option) => this.service.buildValueText("Kit name", v.label as string),
            }
          });
          const kitLotNoOption = (lotNo: string) => ({
            label: "Kit Lot No.",
            value: lotNo,
            other: {
              obs: (v: Option) =>
                this.service.buildValueText("Kit lot number", v.value as string),
              onclick: (val: Option) => {
                keyboard({
                  id: 'lot',
                  helpText: 'Enter Lot Number',
                  type: FieldType.TT_TEXT,
                  isRequired: () => true
                }, 
                (v: Option) => {
                  val.value = v.value
                })
              },
            },
          });
          const kitExpiryDateOption = (expiryDate: string) => ({
            label: "Kit Expiry Date",
            value: expiryDate,
            other: {
              obs: (v: Option) =>
                this.service.buildValueDate("kit expiry date", v.value as string),
              onclick: (val: Option) => {
                keyboard(
                  {
                    id: "end_user",
                    helpText: "Kit Expiry Date",
                    type: FieldType.TT_FULL_DATE,
                    isRequired: () => true,
                    validation: (v: Option) => {
                      if (new Date(v.label) < new Date(this.service.date)) {
                        return [
                          "Expiry date cannot be less than current date " +
                            toDate(this.service.date),
                        ];
                      }
                      return null;
                    },
                  },
                  (v: Option) => {
                    val.value = v.value
                  }
                );
              },
            },
          });
          const enteredKits = f.test_kit === null ? [] : f.test_kit;
          return testResults
            .filter((t) => t.value != "")
            .map((t) => {
              const existingOption = find(enteredKits, { label: t.label });
              if (existingOption) return existingOption;
              return {
                label: t.label,
                value: "",
                other: {
                  fields: [
                    kitNameOption(t.other.shortName),
                    kitLotNoOption(t.other.lotNo),
                    kitExpiryDateOption(t.other.expiryDate)
                  ]
                }
              };
            });
        },
      };
    },
    testResultsField(): Field {
      const { getLotNo, getExpiryDate, initKits } = TestKit()
      const isStatus = (l: Option[], test: string, status: string) =>
        find(l, {
          value: status,
          other: { id: test },
        })
          ? true
          : false;
      return {
        id: "test_results",
        helpText: "Test results",
        type: FieldType.TT_MULTIPLE_YES_NO,
        config: {
          requireAllValues: false
        },
        summaryMapValue: (v: Option) => {
          const resultMap: Record<string, string> = {
            "Positive": "Reactive",
            "Negative": "Non-reactive"
          }
          if (v.value != "") {
            return {
              label: v.label, 
              value: resultMap[v.value] || v.value
            } 
          }
        },
        beforeNext: async (v: Option[], f: any) => {
          const isInconclusive = (isStatus(v, "test_2", "Negative") &&
              isStatus(v, "test_1_repeat", "Positive")) ||
            (isStatus(v, "test_2", "Positive") &&
              isStatus(v, "test_3", "Negative"))
          if (/inconclusive/i.test(f.last_hiv_result.value) && isInconclusive) {
            await infoActionSheet(
              "Inconclusive Re-Test Result",
              "",
              "Please collect DBS sample.",
              [{ name: "Ok", slot: "start" }]
            );
            return true
          }
          if (isInconclusive) {
            await this.inclonclusiveResultDialog();
            return true
          }
          const isPositive = isStatus(v, "test_1", "Positive") && 
            isStatus(v, "test_2", "Positive") &&
            isStatus(v, "test_3", "Positive")
          
          if (isPositive && this.facts.age <= 0) {
            await infoActionSheet(
              "<12 months old all HIV tests reactive",
              "",
              "Please collect DBS sample for EID",
              [{ name: "Ok", slot: "start" }]
            );
          }

          if (f.accesspoint_type.value != "Community" && this.threeTierHIVtestNotComplete(v)) {
            if (!(await alertConfirmation("Are you sure you want to save incomplete HIV results?",
              { header: "Incomplete HIV results!" }
            ))) {
              return false
            }
          }

          return true;
        },
        onValueUpdate: (l: Option[], v: any, f: any) => {
          // Manage Three test algorithm for HIV results
          if (
            this.testOffered(f, "HIV") &&
            f.accesspoint_type.value === "Health Facility"
          ) {
            const results: Option[] = [...l];
            const testOneIsPositive = isStatus(results, "test_1", "Positive");
            const testTwoIsPositive = isStatus(results, "test_2", "Positive");
            const disable = (test: string, disabled: boolean) => {
              results.forEach((r) => {
                if (r.other.id === test) {
                  r.disabled = disabled;
                  if (disabled) r.value = "";
                }
              });
            };
            if (testOneIsPositive) {
              disable("test_2", false);
            } else {
              disable("test_2", true);
              disable("test_3", true);
              disable("test_1_repeat", true);
              return results;
            }
            if (testOneIsPositive && testTwoIsPositive) {
              disable("test_3", false);
              disable("test_1_repeat", true);
              return results;
            }
            if (
              testOneIsPositive &&
              !testTwoIsPositive &&
              l.some((d) => d.other.id === "test_2" && d.value != "")
            ) {
              disable("test_1_repeat", false);
              disable("test_3", true);
            }
            return results;
          }
          return l;
        },
        validation: (v: Option[]) => {
          // Test 1, Test 2 and Test 1 Repeat and Test 3 are optional
          if (!v || v.some((d) => {
              return /syphilis|hepatitis|test_1/i.test(`${d?.other?.id}`) && d.value === "" && !d.disabled
          })) {
            return ["Result entry incomplete!"];
          }
          return null;
        },
        finalComputedValue: (v: Option[]) => {
          return {
            obs: v
              .filter((d) => d.value && typeof d.other.obs === "function")
              .map((d) => d.other.obs(d)),
          };
        },
        init: async () => {
          await initKits()
          return true
        },
        condition: (f: any) => f.tests_offered.length,
        options: (f: any) => {
          const option = (params = {} as any) => {
            return {
              label: params.name,
              value: "",
              disabled: typeof params?.disabled === "boolean" ? params.disabled : false,
              other: {
                id: params?.id || "",
                concept: params?.concept || "",
                shortName: params?.shortName || "",
                category: params?.category || "",
                expiryDate: params?.expiryDate || "",
                lotNo: params?.lotNo || "",
                obs: typeof params?.obs === "function" ? params.obs : undefined,
                accessPoint: params?.accessPoint || "*",
                values: [
                  { label: "Reactive", value: "Positive" }, 
                  { label: "Non-reactive", value: "Negative" }
                ]
              }
            }
          }
          const resultModel: Option[] = [
            option({
              id: "test_1",
              name: "HIV Test 1 (Determine)",
              category: "HIV",
              shortName: "Determine",
              lotNo: getLotNo(KitProp.DETERMINE),
              expiryDate: getExpiryDate(KitProp.DETERMINE),
              concept: "Test 1",
              obs: (v: Option) =>
                this.service.buildValueCoded(v.other.concept, v.value),
            }),
            option({
              id: "test_2",
              name: "HIV Test 2 (Unigold)",
              category: "HIV",
              disabled: true,
              accessPoint: "Health Facility",
              shortName: "Unigold",
              concept: "Test 2",
              lotNo: getLotNo(KitProp.UNIGOLD),
              expiryDate: getExpiryDate(KitProp.UNIGOLD),
              obs: (v: Option) =>
                this.service.buildValueCoded(v.other.concept, v.value),
            }),
            option({
              id: "test_1_repeat",
              name: "HIV Test 1 (Determine) Repeat",
              category: "HIV",
              disabled: true,
              shortName: "Determine (Repeat)",
              accessPoint: "Health Facility",
              concept: "Immediate Repeat Test 1 Result",
              lotNo: getLotNo(KitProp.DETERMINE),
              expiryDate: getExpiryDate(KitProp.DETERMINE),
              obs: (v: Option) =>
                this.service.buildValueCoded(v.other.concept, v.value),
            }),
            option({
              id: "test_3",
              name: "HIV Test 3 (Bioline)",
              category: "HIV",
              disabled: true,
              shortName: "Bioline",
              accessPoint: "Health Facility",
              concept: "Test 3",
              lotNo: getLotNo(KitProp.BIOLINE),
              expiryDate: getExpiryDate(KitProp.BIOLINE),
              obs: (v: Option) =>
                this.service.buildValueCoded(v.other.concept, v.value),
            }),
            option({
              id: 'syphilis',
              name: "Syphilis Test Result",
              category: "Syphilis",
              shortName: "Syphilis",
              concept: "Syphilis Test Result",
              lotNo: getLotNo(KitProp.SYPHILIS),
              expiryDate: getExpiryDate(KitProp.SYPHILIS),
              obs: (v: Option) =>
                this.service.buildValueCoded(v.other.concept, v.value),
            }),
            option({
              id: 'hepatitis',
              name: "Hepatitis B Test Result",
              category: "Hepatitis B",
              shortName: "Hepatitis B",
              concept: "Hepatitis B Test Result",        
              lotNo: getLotNo(KitProp.HEPATITIS),
              expiryDate: getExpiryDate(KitProp.HEPATITIS),
              obs: (v: Option) =>
                this.service.buildValueCoded(v.other.concept, v.value),
            }),
          ];
          const accessPoint = f.accesspoint_type.value;
          const prevResults = Array.isArray(f.test_results)
            ? f.test_results
            : [];
          // Merge previous entered results to default result model
          const results: Option[] = resultModel.reduce(
            (all: any, defaultOption: Option) => {
              const existingResult = find(prevResults, {
                label: defaultOption.label,
              });
              if (existingResult) {
                if (
                  existingResult.other.accessPoint != "*" &&
                  existingResult.other.accessPoint != accessPoint
                ) {
                  existingResult.value = "";
                }
                all.push(existingResult);
              } else {
                all.push(defaultOption);
              }
              return all;
            },
            [] as Option[]
          );
          // Filter options by selected tests offered and access point field values
          return f.tests_offered.reduce((all: Option[], c: Option) => {
            const options: Option[] = results.filter(
              (r: Option) =>
                r.other.category === c.value &&
                (r.other.accessPoint === accessPoint ||
                  r.other.accessPoint === "*")
            );
            return options.length ? all.concat(options) : all;
          }, []);
        },
      };
    },
    hivResultHiddenField(): Field {
      let result = "";
      return {
        id: "hiv_result",
        helpText: "HIV Result",
        type: FieldType.TT_HIDDEN,
        appearInSummary: () => false,
        condition: (f: any) => this.testOffered(f, "HIV"),
        onload: (f: any) => {
          let hasLastTestPerfomed = f.last_hiv_perfomed != null;
          let hasLastHivResult = f.last_hiv_result != null;
          let testOneResult = "";
          let testTwoResult = "";
          let testThreeResult = "";
          let testOneRepeat = "";
          const testResults = (f.test_results||[]);
          const isCommunity = f.accesspoint_type.value === "Community";
          const lastHIVResult = () => f.last_hiv_result?.value;
          const lastTestPerfomed = () => f.last_hiv_perfomed?.value;
          testResults.forEach((r: Option) => {
            switch (r.other.id) {
              case "test_1":
                testOneResult = r.value as string;
                break;
              case "test_2":
                testTwoResult = r.value as string;
                break;
              case "test_3":
                testThreeResult = r.value as string;
                break;
              case "test_1_repeat":
                testOneRepeat = r.value as string;
                break;
            }
          });
          const inconclusiveNegativeForPreviousPositiveProfessional = () =>
            hasLastHivResult &&
            hasLastTestPerfomed &&
            lastHIVResult() === "Positive" &&
            lastTestPerfomed() === "Professional" &&
            (testOneResult === "Negative" ||
              testTwoResult === "Negative" ||
              testThreeResult === "Negative");
          const results: any = {
            // TODO: Replace "Incomplete" with "Testing not completed"
            Incomplete: () => [
              f.accesspoint_type.value != "Community",
              this.threeTierHIVtestNotComplete(testResults)
            ],
            Negative: () => [
              !inconclusiveNegativeForPreviousPositiveProfessional(),
              testOneResult === "Negative" ||
                testTwoResult === "Negative" ||
                testThreeResult === "Negative" ||
                testOneRepeat === "Negative",
            ],
            Positive: () => [!isCommunity, testThreeResult === "Positive"],
            Inconclusive: () => [
              !isCommunity,
              inconclusiveNegativeForPreviousPositiveProfessional() ||
                (testTwoResult === "Negative" &&
                  testOneRepeat === "Positive") ||
                (testTwoResult === "Positive" &&
                  testThreeResult === "Negative"),
            ],
            "Refer client to HIV Clinic": () => [
              isCommunity,
              testOneResult === "Positive",
            ],
          };
          result = Object.keys(results).reduce((finalResult: any, key: any) => {
            if (results[key]().every(Boolean)) return key;
            return finalResult;
          }, "Not indicated");
        },
        defaultValue: () => {
          if (result) return this.toOption(result);
        },
        finalComputedValue: () => {
          if (result) {
            return {
              obs: this.service.buildValueCoded("HIV status", result),
            };
          }
        },
      };
    },
    hivPatientCategoryField(): Field {
      let hivStatusCategory = "N/A";
      return {
        id: "patient_category",
        helpText: "Result given to client",
        type: FieldType.TT_HIDDEN,
        onload: (f: any) => {
          if (!f.hiv_result) return;
          const newHivStatus = f.hiv_result.value;
          const clientAge = this.facts.age;
          const lastHivResult = f.last_hiv_result.value;
          const lastTypeOfTestPerformed =
            f.last_hiv_perfomed != null ? f.last_hiv_perfomed.value : "";
          const lastTimeHivTestedInMonths =
            f.last_time_hiv_tested != null
              ? f.last_time_hiv_tested.other.monthsAgoInt
              : -1;

          const hivCategoryRef: Record<string, boolean[]> = {
            "New exposed infant": [
              clientAge <= 0,
              newHivStatus === "Positive",
              /exposed|inconclusive|negative/i.test(lastHivResult) ||
                (lastHivResult === "Positive" &&
                  /initial professional|self/i.test(lastTypeOfTestPerformed)),
            ],
            "New Positive": [
              newHivStatus === "Positive",
              /never tested|negative|exposed|inconclusive/i.test(
                lastHivResult
              ) ||
                (lastHivResult === "Positive" &&
                  /initial professional|self/i.test(lastTypeOfTestPerformed) &&
                  lastTimeHivTestedInMonths <= 12),
            ],
            "Positive Re-test": [
              newHivStatus === "Positive",
              lastHivResult === "Positive",
              lastTypeOfTestPerformed === "Professional",
              lastTimeHivTestedInMonths <= 12,
            ],
            "Inconclusive Re-test": [
              lastTypeOfTestPerformed === "Professional",
              lastTimeHivTestedInMonths <= 12,
              newHivStatus === "Inconclusive",
            ],
            "New Negative": [newHivStatus === "Negative"],
            "New Inconclusive": [
              newHivStatus === "Inconclusive",
              (/self/i.test(lastTypeOfTestPerformed) && /invalid/i.test(lastHivResult)) ||
              /never tested|negative|exposed infant/i.test(lastHivResult) ||
                (lastHivResult === "Positive" &&
                  /self|initial professional/i.test(lastTypeOfTestPerformed)),
            ],
          };
          hivStatusCategory = Object.keys(hivCategoryRef).reduce(
            (status: string, key: string) =>
              hivCategoryRef[key].every(Boolean) ? key : status,
            "N/A"
          );
        },
        defaultValue: () => {
          if (hivStatusCategory != "N/A")
            return this.toOption(hivStatusCategory);
        },
        finalComputedValue: () => {
          if (hivStatusCategory != "N/A") {
            return {
              obs: this.service.buildValueCoded("HIV group", hivStatusCategory),
            };
          }
        },
        condition: (f: any) => f.hiv_result.value,
      };
    },
  },
});
